
'use client'
import Link from "next/link";
import TypewriterClient from '../../../';
export default function Login() {
    return (
        <section className="w-screen h-screen flex flex-col gap-[20px] w-full justify-center items-center">
            <TypewriterClient />
            <div className="flex flex-col  w-[200px] justify-center items-center">
                <form className="flex flex-col gap-[20px]">
                    <input className="p-2 rounded bg-[#ffffff] placeholder:text-gray-500 text-[#181818]" type="email" placeholder="Email" />
                    <input className="p-2 rounded bg-[#ffffff] placeholder:text-gray-500 text-[#181818]" type="password" placeholder="Password" />
                    <button className="bg-[#ffffff] text-black p-2 rounded hover:bg-[#cccccc] cursor-pointer transition duration-300 ease-in-out text-center w-full" type="submit">Login</button>
                </form>
                <Link className="hover:text-blue-500" href="/auth/register">Olvidaste tu contrasenÌƒa?</Link>
            </div>
        </section>
    );
}